
.btn-container {
  color: aquamarine;
  font-size: 24px;
}

.header-container {
  color: aquamarine;
  font-size: 24px;
}

.smscode-wrapper label {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
          justify-content: center;
}
.code-list {
  display: -webkit-inline-box;
  display: -webkit-inline-flex;
  display: inline-flex;
  border: 1px solid #c4c2cf;
  border-radius: 4px;
  background-color: #fff;
}
.code-list li {
  display: block;
  width: 0.57rem;
  height: 0.4rem;
  line-height: 0.4rem;
  font-size: 0.2rem;
  text-align: center;
}
.code-list li + li {
  border-left: 1px solid #c4c2cf;
}
.code-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  -webkit-transform: scale(0);
          transform: scale(0);
}

.m-cell {
  position: relative;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
  background-color: #fff;
  padding-right: 0.15rem;
}
.m-cell + .m-cell::before {
  content: " ";
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  height: 1px;
  border-top: 1px solid #C7C7C7;
  color: #C7C7C7;
  -webkit-transform-origin: 0 0;
          transform-origin: 0 0;
  -webkit-transform: scaleY(0.5);
          transform: scaleY(0.5);
}
.m-cell-bd {
  -webkit-box-flex: 1;
  -webkit-flex: auto;
          flex: auto;
}
.m-cell-label {
  display: block;
  min-width: 0.75rem;
  word-wrap: break-word;
  word-break: break-all;
  padding-left: 0.12rem;
}
.m-cell-label {
  line-height: 0.44rem;
}
.m-cell-ft {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  text-align: right;
  color: #999;
}
.m-input-has-right-full {
  padding: 0;
}
.my-input {
  display: inline-block;
  width: 100%;
  border: 0;
  outline: 0;
  -webkit-appearance: none;
  background-color: transparent;
  font-size: 0.14rem;
  color: inherit;
  height: 0.44rem;
  padding: 0.1rem 0;
}
.my-input::-webkit-outer-spin-button,
.my-input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.input-right-full {
  margin-left: 5px;
  height: 0.44rem;
  vertical-align: middle;
}
.input-right-full img {
  height: 0.44rem;
}
.icon-eye {
  display: inline-block;
  position: relative;
  top: 0.02rem;
  line-height: 1;
  color: transparent;
  margin-right: 0.1rem;
}
.icon-eye:after {
  content: " ";
  position: absolute;
  top: 0;
  left: 0;
  width: 0.17rem;
  height: 0.12rem;
  background: url(data:image/png;base64,) no-repeat;
  background-size: contain;
}
.icon-eye-password::after {
  background-image: url(data:image/png;base64,);
}
